*,:after,:before{margin:0;padding:0}body,html{background-color:#f3f3f3}body{overflow-y:scroll}fieldset,img{border:0}address,caption,cite,code,dfn,th,var{font-style:normal;font-weight:400}caption,th{text-align:left}q:after,q:before{content:""}abbr,acronym{border:0;font-variant:normal}input,select,textarea{font-weight:inherit}button,input,select,textarea{font-family:inherit;font-size:inherit}textarea{min-height:40px}button::-moz-focus-inner,input::-moz-focus-inner,select::-moz-focus-inner{border:0;padding:0}legend{color:#000}code,kbd,pre,samp,tt{font-family:Consolas,Liberation Mono,Menlo,Courier,monospace;font-size:12px;line-height:18px}img,svg{vertical-align:top;overflow:visible}hr{margin:4px 0;padding:0;height:1px;border:none;background-color:#e6e6e6}