.spinner{border:2px solid #4b9fd5;border-radius:50%;animation:spin .75s linear infinite}.spinner,.spinner-placeholder{position:relative;vertical-align:middle;width:16px;height:16px}.spinner-placeholder{display:inline-block;visibility:hidden}.spinner:after,.spinner:before{left:-2px;top:-2px;display:none;position:absolute;content:"";width:inherit;height:inherit;border:inherit;border-radius:inherit}.spinner,.spinner:after,.spinner:before{display:inline-block;box-sizing:border-box;border-color:#4b9fd5 transparent transparent;animation-duration:1.2s}.spinner:before{transform:rotate(120deg)}.spinner:after{transform:rotate(240deg)}.spinner-margin{margin:10px}@keyframes spin{0%{transform:rotate(0deg)}to{transform:rotate(1turn)}}