.badge{display:inline-block;vertical-align:middle;height:20px;line-height:19px;padding:0 8px;border-radius:10px;font-size:12px;min-width:10px;letter-spacing:.03em;color:#fff;white-space:nowrap;text-align:center;background-color:#4b9fd5}.badge:empty{display:none}a.badge:active,a.badge:focus,a.badge:hover{color:#fff}a.badge{border-bottom:none}.badge-medium{height:24px;line-height:24px;letter-spacing:.01em}.list-group-item-heading>.badge,.list-group-item>.badge{float:right;margin:3px}.list-group-item-heading>.badge+.badge,.list-group-item>.badge+.badge{margin-right:5px}.badge-normal-size{font-size:inherit;letter-spacing:inherit}.badge-tiny-height{height:16px!important;line-height:16px!important}.badge-new{height:18px;font-size:10px;text-transform:uppercase;background-color:#cae3f2;color:#0e516f;font-weight:600}.badge-muted{background-color:#cdcdcd;color:#777}.badge-ok,.badge-success{background-color:#0a0}.badge-warn,.badge-warning{background-color:#ed7d20}.badge-danger,.badge-error{background-color:#d4333f}.badge-danger-light{border:1px solid #ebccd1!important;background-color:#f2dede;color:#a94442}a.badge-danger-light:active,a.badge-danger-light:focus,a.badge-danger-light:hover{color:#a94442}.badge-focus{border:1px solid #faebcc;background-color:#fcf8e3;font-weight:400}.badge-focus,a.badge-focus:active,a.badge-focus:focus,a.badge-focus:hover{color:#8a6d3b}.badge-secondary{background-color:#b4b4b4}.outline-badge{display:inline-block;vertical-align:middle;height:20px;line-height:19px;padding:0 8px;border:1px solid #cdcdcd;border-radius:10px;box-sizing:border-box;background-color:#cdcdcd;font-size:12px;font-weight:400;white-space:nowrap}.outline-badge.active{color:#444}.outline-badge.active,.outline-badge.badge-info{border-color:#4b9fd5;background-color:#cae3f2}.outline-badge.badge-icon{padding-left:4px}.outline-badge.badge-icon svg{height:18px}