.list-group{margin-bottom:20px;padding-left:0}.list-group-item,button.list-group-item{position:relative;z-index:2;display:block;margin-bottom:-1px;padding:5px 10px;border:1px solid transparent;width:100%;box-sizing:border-box;text-align:left}.list-group-item.depth-1{padding-left:31px}.list-group-item.depth-2{padding-left:51px}.list-group-item.depth-3{padding-left:71px}.list-group-item:last-child{margin-bottom:0}.list-group-item.active,.list-group-item.active:focus,.list-group-item.active:hover{z-index:3;border-color:#4b9fd5!important;background-color:#cae3f2}.list-group-item:hover{z-index:3;border-color:#4b9fd5!important}.list-group-item+.list-group-item{border-top-color:#e6e6e6}a.list-group-item{color:#444;transition:none}.list-group-item-heading{margin-top:5px;margin-bottom:5px}.list-group-item-heading:after{content:"";display:table;clear:both}